import React, { useState, useEffect } from 'react';
import { Route, Routes } from 'react-router-dom';
import { HelmetProvider } from 'react-helmet-async';
import ProjectsPage from './routes/ProjectsPage';
import Header from './widgets/Header.js';
import HomeSceneFiber from './routes/HomeSceneFiber';
import Blog from './routes/Blog';
import Footer from './widgets/Footer.js';
import './styles/App.css';

const App = () => {
  const [pageTitle, setPageTitle] = useState('');

  useEffect(() => {
    document.title = pageTitle || 'Alex Kahn';
  }, [pageTitle]);

  return (
    <div className="app-container">
      <HelmetProvider>
        <Header />
        <div className="main-content">
          <Routes>
            <Route path="/" element={<HomeSceneFiber setPageTitle={setPageTitle} basename="/"/>} />
            <Route path="/projects" element={<ProjectsPage setPageTitle={setPageTitle} />} basename="/projects" />
            <Route path="/blog" element={<Blog setPageTitle={setPageTitle} />} basename="/blog" />
          </Routes>
        </div>
        <Footer />
      </HelmetProvider>
    </div>
  );
};

export default App;
